import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide
  },
  data() {
    return {
      info: {}
    }
  },
  methods: {
    getInfo() {
      http.post('/wallet/auditing-info', {
        id: this.$route.params.recodeId,
      }, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.info = res.content
      })
    }
  },
  created() {
    this.getInfo()
  }
}



